import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import {mapState,mapMutations} from 'vuex';  

export default {
	components: {
		uniNoticeBar,uniSwiperDot
	},
	computed: {
		...mapState(['hasLogin','userInfo','firstMenu'])
	},
	data() {
		return {
			dotsStyles: {
				backgroundColor: '#DBDBDB',
				border: '1px #DBDBDB solid',
				color: '#fff',
				selectedBackgroundColor: '#00A390',
				selectedBorder: '1px #00A390 solid'
			},
			
			//高度 3个： 760,2: 520, 1:  140 rpx
			tgFrameHeight: 'height: 520rpx !important;',
			kjFrameHeight: 'height: 520rpx !important;',
			
			//团购
			tgCurrent: 0,
			kjCurrent: 0,
			
			//爆款
			bkCurrent: 0,	
				
			
			//头部菜单
			isClickChange: false,
			TabCur: 0,
			scrollLeft: 0,
			newsitems: [], //每页加载的数据
			tabBars: this.firstMenu,
			
			//秒杀倒计时
			sekilltime:{
				hour: '00',
				min: '00',
				sec: '00',
			},
			timer: null,
			seconds: 0, //剩余支付时间
			
			//瀑布流
			pageIndex: 1,
			hasMore: true,
			pageLoading: false,
		};
	},
	methods: {
		...mapMutations(['setFirstMenu']),
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {				
			//加载一级分类
			this.$request.ModelHome.getCategoryByPid(0).then(result => {
				if(result != null && result.length > 0){
					this.setFirstMenu(result);
					this.tabBars = result;
					//加载第一个菜单的数据
					this.loadSubData(result[0].id);
				}
			})
		},
		async loadSubData(pid){ //pid 一级分类ID
			let subData = this.newsitems[this.TabCur];
			if(subData == null){
				subData = {};
			}
			// 加载配置项
			this.$request.ModelHome.getConfig(pid).then(config => {
				subData.config = config;
				//秒杀倒计时
				this.startTimeup(config.seckillLeftSec);
			});
			
			//加载子菜单
			this.$request.ModelHome.getCategoryByPid(pid).then(menu => {
				subData.menu = menu;
			});
			
			//加载banner
			this.$request.ModelHome.getBanner(pid).then(banner => {
				subData.banner = banner;
			});
			
			//加载文章
			this.$request.ModelHome.getArticeList(pid).then(articles => {
				subData.articles = articles;	
			});
			
			//获取秒杀
			this.$request.ModelHome.getSeckill({classifyPid: pid}).then(seckill => {
				if(seckill && seckill.records && seckill.records.length>0){
					subData.seckill = seckill.records;
				}
			});
			
			//获取砍价
			this.$request.ModelHome.getBargin({classifyPid: pid,pageSize: 6}).then(bargin => {
				this.kjFrameHeight = 'height: 0px !important;'
				if(bargin && bargin.records && bargin.records.length>0){
					//高度 3个： 760,2: 520, 1:  140 rpx
					if(bargin.records.length == 1){

						this.kjFrameHeight = 'height: 260rpx !important;'




					}else{

						this.kjFrameHeight = 'height: 520rpx !important;'




					}
					let info = [],temp = [];
					let index = 0;
					for(var i=0;i<bargin.records.length;i++){
						if(index > 2){
							info.push(temp);
							temp = [];index = 0;
						}
						temp.push(bargin.records[i]);
						index ++;
					}
					if(temp.length != 0){
						info.push(temp);
					}
					subData.bargin = info;
					this.kjCurrent = 0;
				}
			});
			
			//团购优惠
			this.$request.ModelHome.getGroupBuy({classifyPid: pid,pageSize:9}).then(groupbuy => {
				this.tgFrameHeight = 'height: 0px !important;'
				if(groupbuy && groupbuy.records && groupbuy.records.length>0){
					//高度 3个： 760,2: 520, 1:  140 rpx
					if(groupbuy.records.length == 1){

						this.tgFrameHeight = 'height: 260rpx !important;'




					}else if(groupbuy.records.length == 2){

						this.tgFrameHeight = 'height: 520rpx !important;'




					}else{

						this.tgFrameHeight = 'height: 760rpx !important;'




					}
					let info = [],temp = [];
					let index = 0;
					for(var i=0;i<groupbuy.records.length;i++){
						if(index > 2){
							info.push(temp);
							temp = [];index = 0;
						}
						temp.push(groupbuy.records[i]);
						index ++;
					}
					if(temp.length != 0){
						info.push(temp);
					}
					subData.groupbuy = info;
					this.tgCurrent = 0;
				}
			});
			
			//爆款推荐
			this.$request.ModelHome.getGoodsList({classifyPid:pid,pageSize:9,orderBySort:true,direction: true}).then(bkgoods => {
				if(bkgoods && bkgoods.records && bkgoods.records.length > 0){
					let info = [],temp = [];
					let index = 0;
					for(var i=0;i<bkgoods.records.length;i++){
						if(index > 2){
							info.push(temp);
							temp = [];index = 0;
						}
						bkgoods.records[i].index = i + 1;
						temp.push(bkgoods.records[i]);
						index ++;
					}
					if(temp.length != 0){
						info.push(temp);
					}
					subData.bkgoods = info;
					this.bkCurrent = 0;
				}
			});
			
			//热销
			this.pageIndex = 1;this.pageLoading = true;this.hasMore=true;
			this.$request.ModelHome.getGoodsList({classifyPid:pid,pageSize:10,pageIndex: this.pageIndex,orderBySell:true,direction: true}).then(rxgoods => {
				this.pageLoading = false;
				if(rxgoods && rxgoods.records && rxgoods.records.length > 0){
					subData.rxgoods = rxgoods.records;
				}
			});
			this.newsitems[this.TabCur] = subData;
			this.$forceUpdate();
		},
		moreRex(){ //加载热销
			let firstCat = this.tabBars[this.TabCur];
			this.pageIndex = this.pageIndex + 1;
			this.pageLoading = true
			this.$request.ModelHome.getGoodsList({classifyPid:firstCat.id,pageSize:10,pageIndex: this.pageIndex,orderBySell:true,direction: true}).then(rxgoods => {
				this.pageLoading = false;
				if(rxgoods && rxgoods.records && rxgoods.records.length > 0){
					let subData = this.newsitems[this.TabCur];
					if(!subData.rxgoods){
						subData.rxgoods = [];
					}
					rxgoods.records.map(item => {
						subData.rxgoods.push(item);
					});
					this.newsitems[this.TabCur] = subData;
					this.$forceUpdate();
				}else{
					this.hasMore = false;
				}
			});
		},
		navToLogin(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
			}
			uni.navigateTo({  
				url
			})  
		},
		navTo(url){
			uni.navigateTo({  
				url
			})  
		},
		//详情页
		navToDetailPage(id, source) {
			source = source? source:'';
			uni.navigateTo({
				url: `/pages/product/product?id=${id}&source=${source}`
			})
		},
		navToCategory(){
			uni.switchTab({
				url: `/pages/category/category`
			})
		},
		//团购切换
		async tgSwitch(e){
			this.tgCurrent = e.detail.current
		},
		async kjSwitch(e){
			this.kjCurrent = e.detail.current
		},
		//爆款
		async bkSwitch(e){
			this.bkCurrent = e.detail.current
		},
		// 头部菜单切换
		async tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			this.isClickChange = true;

			let firstCat = this.tabBars[this.TabCur];
			this.loadSubData(firstCat.id);
		},
		async changeTab(e) {
			let index = e.target.current;
							
			if (this.isClickChange) {
				this.TabCur = index;
				this.isClickChange = false;
				return;
			}
			
			this.scrollLeft = (index - 1) * 60;
			this.isClickChange = false;
			this.TabCur = index;
			
			let firstCat = this.tabBars[index];
			this.loadSubData(firstCat.id);
		},
		getnewsMore(){
			let firstCat = this.tabBars[this.TabCur];
			this.navTo('/pages/article/list');
		},
		bannerClick(item){
			if(item.href){
				this.navTo(item.href);	
			}
		},
		countDown() {
			let seconds = this.seconds
			let [day, hour, minute, second] = [0, 0, 0, 0]
			if (seconds > 0) {
				// day = Math.floor(seconds / (60 * 60 * 24))
				hour = Math.floor(seconds / (60 * 60)) - day * 24
				minute = Math.floor(seconds / 60) - day * 24 * 60 - hour * 60
				second = Math.floor(seconds) - day * 24 * 60 * 60 - hour * 60 * 60 - minute * 60
			} else {
				clearInterval(this.timer);
			}
			if (day < 10) {
				day = '0' + day
			}
			if (hour < 10) {
				hour = '0' + hour
			}
			if (minute < 10) {
				minute = '0' + minute
			}
			if (second < 10) {
				second = '0' + second
			}
			// this.d = day
			this.sekilltime.hour = hour;
			this.sekilltime.min = minute;
			this.sekilltime.sec = second;
		},
		startTimeup(timeup){
			console.log('timeup',timeup);
			if(timeup == null || timeup <= 0){
				return;
			}
			this.seconds = timeup;
			this.countDown()
			if(this.timer){
				clearInterval(this.timer);
			}
			this.timer = setInterval(() => {
				this.seconds--
				if (this.seconds < 0) {
					this.timeUp()
					return
				}
				this.countDown()
			}, 1000)
		},
		scroll(){
			console.log('scroll');
			if(this.hasMore && this.pageLoading ==false ){
				this.moreRex();	
			}
		}
	},
	beforeDestroy() {
		clearInterval(this.timer);
	},

























	onShareAppMessage() { //设置分享
		return {
			title: '欢迎来到社集优选',
			path: '/pages/index/index?inviteUserId=' + this.userInfo.id
		}
	},
	onLoad(options){ //进入首页设置邀请ID
		if(options.scene){
			let scene=decodeURIComponent(options.scene);
			let userId=scene.split("&")[0];
			uni.setStorage({//缓存用户登陆状态
			    key: 'inviteUserId',  
			    data: userId  
			})
		}
		if(options.inviteUserId){
			uni.setStorage({//缓存用户登陆状态
			    key: 'inviteUserId',  
			    data: options.inviteUserId  
			})
		}
		this.loadData();
	},
}
